.div6542{
	width: 100%;
	height: 600px;
	display: flex;
	justify-content: center;
	align-items: center;
	 
}


.layer{
	height: 80px;
	width: 80px; 
	left: 50%;
	top: 50%;
	transform: translate(-0%,-50%) rotate(0deg);
	animation: rotate 1.5s linear infinite;
	border-left: 1px solid transparent;
	border-top: 7px solid #8a2be2;
	border-radius: 50%;
}


@keyframes rotate{
	100%{
		transform: translate(-0%,-50%) rotate(-360deg);
	}
}