.attendaceDiv {
   border: 2px solid black;
   border-radius: 5px;
   margin: 15px;
   padding: 7px;
   background-color: rgb(171, 131, 131);
   justify-content: space-evenly;
   text-align: center;
   align-items: center;
   display: flex;

}


.lectureDiv_Sort {
   border-radius: 15px;
   margin: 15px;
   padding: 15px;
   background-color: rgba(255, 255, 255, 0);
   display: flex;
   gap: 20px;
   flex-direction: column;
   justify-content: space-evenly;
   align-items: center;
 
   box-shadow: 0px 0px 44px -10px rgba(0, 0, 0, 0.75);
   background-color: silver;
   

}

.lectureDiv_main {


   background-color: rgb(255, 255, 255);
   display: flex;
   width: 100%;
   padding: 5px 2px;
   flex-direction: column;
   justify-content: space-evenly;
   align-items: center;


}

.lectureDiv {
   display: grid;
   grid-template-columns: 0.5fr 1.5fr 2.5fr 1.5fr 1.5fr 3.5fr;
   border: none;
   width: 100%;
   align-items: center;

}

 
.indivisualRollNumber{
   text-align: center;
   min-width: 80px;
    
}


.forPresentRoll {
   background-color: rgb(0, 255, 0);
   border: none;
   border-radius: 30px;
   color: black;
}

.forAbsentRoll {
   background-color: rgb(213, 0, 0);
   border: none;
   color: white;
   border-radius: 30px;
}

.DateNInput_div {
   display: flex;
   gap: 20px;
}

.DateNInput {
   display: flex;
   font-size: 14px;
   flex-direction: column;

}

.DateNInput>input {
   border: none;

}

.groupOfButtons {
   display: flex;
   gap: 10px;
}




input[type='radio']:after {
   width: 15px;
   height: 15px;
   border-radius: 15px;
   top: -2px;
   left: -1px;
   position: relative;
   background-color: #d1d3d1;
   content: '';
   display: inline-block;
   visibility: visible;
   border: 2px solid white;
}

input[type='radio']:checked:after {
   width: 15px;
   height: 15px;
   border-radius: 15px;
   top: -2px;
   left: -1px;
   position: relative;
   content: '';
   display: inline-block;
   visibility: visible;
   background-color: #8a2be2;
   border: 2px solid white;
}

.allLectures_div {
    border-radius: 15px;
   padding-top: 20px;
   padding-bottom: 20px;
   margin: 30px 15px;


}

.lectureDiv>span:nth-child(1) {
   position: relative;
   left: 10px;
}

.lectureDiv>span:nth-child(2) {
   position: relative;
   left: 20px;
}

.lectureDiv>span:nth-child(6) {
   display: flex;
   justify-content: center;
   align-items: center;
}

.lecturesController {
   display: flex;
   flex-direction: column;

}

.lecturesController>div {
   display: flex;
