}




.divisionDivforLecture {
   margin-top: 30px;
   background-color: white;
   border-radius: 20px;
   display: flex;
   flex-direction: column;
   padding: 10px;
   gap: 20px;
   box-shadow: 0px 0px 44px -10px rgba(0, 0, 0, 0.75);
   
   background-color: silver;
