.imageNName{
    display: flex;
    flex-direction: column; 
    align-items: center;
    position: relative;
    
    
}
 
.userAcountMainDiv{
    display: grid;
    grid-template-columns:  250px auto  ;
    height: 100vh; 
  }
   
 
.ProfileImg{
    height: 250px;
    width: 250px;
    border-radius: 50%; 
    
}
.imgDiv{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    height: 250px;
    width: 250px;
    border-radius: 50%; 
    -webkit-box-shadow: 0px 0px 111px -33px rgba(137, 43, 226, 0.709);
-moz-box-shadow: 0px 0px 111px -33px rgba(137, 43, 226, 0.662);
box-shadow: 0px 0px 111px -33px rgba(137, 43, 226, 0.644);
}
.div786565{
    margin: 20px 15px ;
    background-color: white;
    border-radius: 25px;
    padding: 15px;
}
 
@media only screen and (max-width: 768px){
    .imgDiv{
        width: 50vw;
        height:50vw;
        position: relative;
        top: 20vw;
        z-index: 2;
        border:2px solid rgb(245, 245, 245);
         
    }
    .ProfileImg{
        width: 50vw;
        height:50vw;
       

    }
    .imageNName{
        
        flex-direction: row;
        justify-content: first baseline;
        margin-top: 20px;
        gap: 15px;

    }
    .imageNName>div:nth-child(2){
        
         font-size: 19px;
        font-weight: bolder;

    }
     
    
    #enNumber{
         text-align: center;
    }
    #ContentCopyIcon{
        font-size: 16px;
        padding-top: 2px;
    }
    .fakeDiv_userDetail{
        height: 250px;
    }
    .profileInfoDiv>button{
margin-top: 20px;
    }
    .profileInfoDiv{
        padding-top: 25vw;
       
    }
    .div486623{
        margin-top: -10px;
       
    }
    .div486623 h3{
           margin-top: 20px; 
           margin-bottom: 10px; 

    }
}