.popupBox {
    width: 60vw;
    position: absolute;
    z-index: 99; 
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 30px 20px;
    background: #892be20a;
   border-radius: 10px;
   border: 2px solid rgba(0, 0, 0, 0.76);
   position: absolute;
   -webkit-backdrop-filter: blur(5px);
   backdrop-filter: blur(3px);
   display: block;
   margin: 0 auto;
   font-size: 18px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   font-weight: bolder;
   -webkit-box-shadow: 0px 0px 50px -9px rgba(0,0,0,0.75);
   -moz-box-shadow: 0px 0px 50px -9px rgba(0,0,0,0.75);
   box-shadow: 0px 0px 50px -9px rgba(0,0,0,0.75);
   gap: 15px;
}
.popupBox>span{
    width: 100%;
    text-align: center;
}
.wanted_btn{
    background-color:  #8a2be2;
    color: white;
}